import { Component, ViewEncapsulation, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { AppService } from '../app.service';
import { MatDialog } from '@angular/material';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../auth/auth.service';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class SignupComponent implements OnInit, OnChanges {
    constructor(private appService: AppService, private dialog: MatDialog, private router: Router, private authService: AuthService) {}

signupForm: FormGroup;
submitDisab;
passSame = true;
 public name: string;
 public email: string;
 public phone: number;
public password: string;

    hasError(e) {
        console.log(e);
    }
    getNumber(e) {
        console.log(e);
    }

    telInputObject(e) {

        this.submitDisab = this.signupForm.valid;
    }

    onCountryChange(e) {
        console.log(e);
    }



    ngOnInit() {
        this.signupForm = new FormGroup({
            fullName : new FormControl('', [Validators.required]),
            email: new FormControl('', [Validators.required, Validators.email]),
            phone: new FormControl('', [Validators.required]),
            checkb : new FormControl('', [Validators.required] ),
            password: new FormControl('', [Validators.required]),
            Cpassword: new FormControl('', [Validators.required])
        });

        this.submitDisab = this.signupForm.valid;

        console.log(this.signupForm.value);

    }

    ngOnChanges(changes: SimpleChanges) {
        console.log(changes);
        this.submitDisab = this.signupForm.valid;
        this.name = this.signupForm.value.fullName;
        this.email = this.signupForm.value.email;
        this.phone = this.signupForm.value.phone;
        this.password = this.signupForm.value.password;
    }
    onFormChange() {
        this.submitDisab = this.signupForm.valid;
        

        if (this.signupForm.controls.password.touched && this.signupForm.controls.Cpassword.touched) {
            if (this.signupForm.value.password == this.signupForm.value.Cpassword) {
                this.passSame = true;

            }else {
                this.passSame = false;

            }
        }

        this.name = this.signupForm.value.fullName;
        this.email = this.signupForm.value.email;
        this.phone = this.signupForm.value.phone;
        this.password = this.signupForm.value.password;
    }
    onSignupSubmit() {
        this.name = this.signupForm.value.fullName;
        this.email = this.signupForm.value.email;
        this.phone = this.signupForm.value.phone;
        this.password = this.signupForm.value.password;

        this.authService.signup(this.name, this.email,this.phone, this.password).subscribe(
            (respData) => {
                this.router.navigate(['/login']);
                console.log(respData);
            }
        );
        this.router.navigate(['/verify']);
    }


}
