import { Component, ViewEncapsulation, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { AppService } from '../app.service';
import { MatDialog } from '@angular/material';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../auth/auth.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-login',
    templateUrl: 'login.component.html',
    styleUrls: ['login.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit, OnChanges {

    constructor(private appService: AppService, private dialog: MatDialog, private authService: AuthService, private router: Router) {}

loginForm: FormGroup;
private email: string;
private password: string;


    ngOnInit() {
        this.loginForm = new FormGroup({

            email: new FormControl('', [Validators.required, Validators.email]),

            password : new FormControl('', [Validators.required] )
        });



    }

    ngOnChanges(changes: SimpleChanges) {
        console.log(changes);

    }
    onFormChange() {
this.email = this.loginForm.value.email;
this.password = this.loginForm.value.password;

    }

    onLoginSubmit() {
        this.router.navigate(['/menucomponent']);
        this.authService.login(this.email, this.password).subscribe(
            (respData) => {
                this.router.navigate(['/dashboard']);
                console.log(respData);
            },
            (err) => {console.log(err); }
        );
    }


}

